import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import WaterApiList from '../../../Components/ApiList/WaterApiList';
import HelpAndSupport from '../../LandingDashboard/LandDashNew/Components/HelpAndSupport'

// import pdfImage from '../../../assets'
import pdfImage from '../../../assets/Images/pdf.png'
import imageIcon from '../../../assets/Images/image.png'
import noImage from '../../../assets/Images/no-photo.png'
import WaterDocumentModal from '../Modals/WaterDocumentModal';
import WaterDocumentModalForOwner from '../Modals/WaterDocumentModalForOwner';
import ViewUploadedDoc from '../Modals/ViewUploadedDoc';
import BarLoader from '../../../Components/Common/BarLoader';
import WaterPaymentModal from '../Modals/WaterPaymentModal';
import WaterApplicationTopButtons from '../../../Pages/Water/ViewApplication/WaterApplicationTopButtons'
import AxiosInterceptors from '../../../Components/GlobalData/AxiosInterceptors';
import { contextVar } from '../../../Components/ContextVar';
import ShimmerEffectInline from '../../../Components/ShimmerEffects/ShimmerEffectInline';


const WaterDocUploadIndex = (props) => {
    const [openDocumentModal, setOpenDocumentModal] = useState(0)
    const [openDocumentModalForOwner, setOpenDocumentModalForOwner] = useState(0)
    const [documentList, setDocumentList] = useState()
    const [ownerDocList, setOwnerDocList] = useState()
    const [openDocviewModal, setOpenDocviewModal] = useState(0)
    const [payloadData, setPayloadData] = useState()
    const [documentUrl, setDocumentUrl] = useState()
    const [loader, setLoader] = useState(false)
    const [isDocUpload, setIsDocUpload] = useState(false)
    const [connectionCharges, setConnectionCharges] = useState(false)
    const [openPaymentModal, setOpenPaymentModal] = useState(false);

    const { api_waterViewDocumetTobeuploaded, header } = WaterApiList();
    const { id } = useParams();

    const navigate = useNavigate()



    useEffect(() => {
        fetchData()
    }, [id])


    const { setBreadCrumbData } = useContext(contextVar)
    useEffect(() => {
        setBreadCrumbData(["Water Dashboard", "Upload Document"])
    }, [])

    const fetchData = () => {
        setLoader(true)
        AxiosInterceptors.post(api_waterViewDocumetTobeuploaded, { "applicationId": id }, header)
            .then((res) => {
                setLoader(false)
                console.log("LIst of document to be uploaded in water", res)
                setDocumentList(res.data.data.documentsList)
                setOwnerDocList(res.data.data.ownersDocList)
                setIsDocUpload(res?.data?.data?.doc_upload_status)
                setConnectionCharges(res?.data?.data)
            })
            .catch((err) => {
                setLoader(false)
                console.log("ERror while fetching list of water doc to be upladed", err)
            })
    }

    const handleUploadBtn = (docName, docVal, refDocName) => {
        const payload = {
            "docName": docName,
            "applicationId": id,
            "docVal": docVal,
            "refDocName": refDocName,
        }
        setPayloadData(payload)
        setOpenDocumentModal(pre => pre + 1)
    }


    const handleUploadBtnForOwner = (ownerName, docName, ownerId, docVal, docCategory) => {
        const payload = {
            "ownerName": ownerName,
            "docName": docName,
            "applicationId": id,
            "ownerId": ownerId,
            "docVal": docVal,
            "docCategory": docCategory
        }
        setPayloadData(payload)
        setOpenDocumentModalForOwner(pre => pre + 1)
    }

    const openDocView = (url) => {
        if (url) {
            setDocumentUrl(url)
            setOpenDocviewModal(prev => prev + 1)
        }
    }

    const handlePayment = () => {
        setOpenPaymentModal(true)
    }

    const category = connectionCharges?.connectionCharges?.charge_category;

    console.log("connectionCharges")

    return (
        <>
            {/* {loader && <BarLoader />} */}

            <WaterApplicationTopButtons active="document" title={"Water Application Document Upload"} applicationNo={connectionCharges?.connectionCharges?.applicationNo} />

            {/* ============WATER PAYMENT============ */}
            {openPaymentModal && <WaterPaymentModal openPaymentModal={setOpenPaymentModal} category={category} paymentData={connectionCharges} />}

            <WaterDocumentModal openDocumentModal={openDocumentModal} payloadData={payloadData} refetch={fetchData} />
            <WaterDocumentModalForOwner openDocumentModal={openDocumentModalForOwner} payloadData={payloadData} refetch={fetchData} />
            <ViewUploadedDoc openDocviewModal={openDocviewModal} documentUrl={documentUrl} />
            <div className='grid grid-cols-12 bg-white poppins animate__animated animate__fadeIn animate__faster'>

                <div className='col-span-12 pt-4 2xl:h-full md:h-screen overflow-y-auto pb-2'>
                    <div className="md:px-2 col-span-12">

                        <div className="float-right m-3 space-x-3">
                            {isDocUpload == true ?
                                connectionCharges?.connectionCharges?.paid_status == false &&
                                <button onClick={() => handlePayment()} class="py-2 px-4 inline-block text-center mb-3 leading-5 text-gray-100 bg-green-500 border border-green-500 hover:text-white hover:bg-green-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0">Pay Now</button>
                                :
                                <div></div>
                            }
                        </div>
                        <div>
                            <h1 class="text-base md:text-xl font-bold md:my-3 text-gray-600 text-center">Water Connection Document List</h1>
                        </div>

                        <div className="">
                            <div className="md:inline-block min-w-full overflow-hidden">
                                {loader ? <> <ShimmerEffectInline /> <ShimmerEffectInline /> </> :
                                    <>
                                        <table className="min-w-full leading-normal border">
                                            <thead className='bg-sky-100'>
                                                <tr className='font-semibold '>
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                        #
                                                    </th>
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                        Document Name
                                                    </th>
                                                    {/* <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase"> Remark </th> */}
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                        View
                                                    </th>
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800 text-center text-sm uppercase">
                                                        Status
                                                    </th>
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                        Upload
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    documentList?.map((e, i = 1) => (
                                                        <tr>
                                                            <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                                <p className="text-gray-900 whitespace-no-wrap">
                                                                    {i + 1}
                                                                </p>
                                                            </td>
                                                            <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                                <p className="text-gray-900 whitespace-no-wrap">
                                                                    {e?.refDocName}{e?.isMadatory == 1 && <span className="text-red-500 font-semibold mx-1">*</span>}
                                                                </p>
                                                            </td>
                                                            <td onClick={() => openDocView(e?.uploadDoc?.doc_path)} className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm" >
                                                                <div className="flex items-center">
                                                                    <div className="flex-shrink-0">
                                                                        <a href="#" className="block relative">
                                                                            {e?.uploadDoc == "" && <img alt="profil" src={noImage} className="mx-auto object-cover rounded-none cursor-not-allowed md:h-10 h-8 md:w-10 w-8" />}
                                                                            {e?.uploadDoc?.doc_path?.split('.').pop() == "pdf" && <img alt="profil" src={pdfImage} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8 " />}
                                                                            {e?.uploadDoc?.doc_path?.split('.').pop() == "jpg" && <img alt="profil" src={imageIcon} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8 " />}
                                                                            {e?.uploadDoc?.doc_path?.split('.').pop() == "jpeg" && <img alt="profil" src={imageIcon} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8 " />}
                                                                            {e?.uploadDoc?.doc_path?.split('.').pop() == "png" && <img alt="profil" src={imageIcon} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8 " />}
                                                                        </a>
                                                                    </div>
                                                                </div>
                                                            </td>
                                                            <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                                <p className="text-center text-black md:font-medium text-xs md:text-sm">
                                                                    {e?.uploadDoc == "" ? <p className="text-red-500">Not Upload</p> :
                                                                        <div>
                                                                            {e?.uploadDoc?.verify_status == 0 && <p className="text-yellow-700">Pending</p>}
                                                                            {e?.uploadDoc?.verify_status == 1 && <p className="text-green-700">Verified</p>}
                                                                            {e?.uploadDoc?.verify_status == 2 && <p className="text-red-700">Rejected</p>}
                                                                        </div>
                                                                    }
                                                                </p>
                                                            </td>
                                                            <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm ">
                                                                {e?.uploadDoc?.verify_status != 1 &&
                                                                    <button onClick={() => handleUploadBtn(e.docName, e.docVal, e.refDocName)} className="border px-2 md:px-4 py-1 md:py-2 hover:text-white hover:bg-indigo-500">{e?.uploadDoc?.verify_status == 2 ? "Re-Upload" : "Upload"}</button>
                                                                }
                                                            </td>
                                                        </tr>
                                                    ))
                                                }
                                            </tbody>
                                        </table>
                                        <p className=' font-semibold text-red-400'> <span className='text-base'> *</span> <span className='text-xs'> Mandatory</span></p>
                                    </>
                                }
                            </div>
                        </div>
                        <div>
                            <h1 class="text-base md:text-xl font-bold my-3 text-gray-600 text-center">Owner Document List</h1>
                        </div>
                        <div className="">
                            <div className="md:inline-block min-w-full overflow-hidden">
                                {loader ? <> <ShimmerEffectInline /> <ShimmerEffectInline /> </> :
                                    <>
                                        <table className="min-w-full leading-normal border">
                                            <thead className='bg-sky-100'>
                                                <tr className='font-semibold '>
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                        #
                                                    </th>
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                        Document Name
                                                    </th>
                                                    {/* <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                Owner Name
                                            </th> */}
                                                    {/* <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase"> Remark </th> */}
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                        View
                                                    </th>
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800 text-center text-sm uppercase">
                                                        Status
                                                    </th>
                                                    <th scope="col" className="px-1 md:px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                                        Upload
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {ownerDocList ?
                                                    ownerDocList?.map((e, i = 1) => (
                                                        <tr>
                                                            <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                                <p className="text-gray-900 whitespace-no-wrap">
                                                                    {i + 1}
                                                                </p>
                                                            </td>
                                                            <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                                <p className="text-gray-900 whitespace-no-wrap">
                                                                    {e?.refDocName}{e?.isMadatory && <span className="text-red-500 font-semibold mx-1">*</span>}
                                                                </p>
                                                                <p className='text-xs md:w-full w-40'>
                                                                    ({e?.ownerName})
                                                                </p>
                                                            </td>
                                                            {/* <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                        <p className="text-gray-900 whitespace-no-wrap">
                                                            {e?.ownerName}
                                                        </p>
                                                    </td> */}
                                                            {/* <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                        <p className="text-gray-900 whitespace-no-wrap">
                                                            {e?.uploadDoc == "" ? <p className="text-red-500">Not Upload</p> :
                                                                <div>
                                                                    {e?.uploadDoc?.verify_status == 0 && <p className="text-green-700">Pending</p>}
                                                                    {e?.uploadDoc?.verify_status == 1 && <p className="text-green-700">Verified</p>}
                                                                    {e?.uploadDoc?.verify_status == 2 && <p className="text-red-700">Rejected</p>}

                                                                </div>
                                                            }
                                                        </p>
                                                    </td> */}
                                                            <td onClick={() => openDocView(e?.uploadDoc?.doc_path)} className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm" >
                                                                <div className="flex items-center">
                                                                    <div className="flex-shrink-0">
                                                                        <a href="#" className="block relative">
                                                                            {e?.uploadDoc == "" && <img alt="profil" src={noImage} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8  cursor-not-allowed" />}
                                                                            {e?.uploadDoc?.doc_path?.split('.').pop() == "pdf" && <img alt="profil" src={pdfImage} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8  " />}
                                                                            {e?.uploadDoc?.doc_path?.split('.').pop() == "jpg" && <img alt="profil" src={imageIcon} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8  " />}
                                                                            {e?.uploadDoc?.doc_path?.split('.').pop() == "jpeg" && <img alt="profil" src={imageIcon} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8  " />}
                                                                            {e?.uploadDoc?.doc_path?.split('.').pop() == "png" && <img alt="profil" src={imageIcon} className="mx-auto object-cover rounded-none md:h-10 h-8 md:w-10 w-8  " />}
                                                                        </a>
                                                                    </div>
                                                                </div>
                                                            </td>
                                                            <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                                <p className="text-center text-black md:font-medium text-xs md:text-sm">
                                                                    {e?.uploadDoc == "" ? <p className="text-red-500">Not Upload</p> :
                                                                        <div>
                                                                            {e?.uploadDoc?.verify_status == 0 && <p className="text-yellow-700">Pending</p>}
                                                                            {e?.uploadDoc?.verify_status == 1 && <p className="text-green-700">Verified</p>}
                                                                            {e?.uploadDoc?.verify_status == 2 && <p className="text-red-700">Rejected</p>}
                                                                            {/* {e?.uploadDoc && <p className="text-green-700">Uploaded</p>} */}

                                                                        </div>
                                                                    }
                                                                    {/* {e.uploadDoc == "" && <p className='bg-red-200 whitespace-nowrap rounded-xl px-2 py-0.5'>Pending</p>}
                                                            {e.uploadDoc != "" && <p className='bg-green-200 rounded-xl py-0.5'>Uploaded</p>} */}
                                                                </p>
                                                            </td>
                                                            <td className="px-1 md:px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                                {e?.uploadDoc?.verify_status != 1 &&
                                                                    <button onClick={() => handleUploadBtnForOwner(e.ownerName, e.docName, e.ownerId, e.docVal, e.refDocName)} className="border px-2 md:px-4 py-1 md:py-2 hover:text-white hover:bg-indigo-500">{e?.uploadDoc?.verify_status == 2 ? "Re-Upload" : "Upload"}</button>
                                                                }
                                                            </td>
                                                        </tr>
                                                    ))
                                                    : <p>No Data Found</p>
                                                }

                                            </tbody>
                                        </table>
                                        <p className=' font-semibold text-red-400'> <span className='text-base'> *</span> <span className='text-xs'> Mandatory</span></p>
                                    </>
                                }
                            </div>
                        </div>


                    </div>
                </div>

                {/* <HelpAndSupport /> */}
            </div>
        </>
    )
}

export default WaterDocUploadIndex